import "babel-polyfill";
import fetch from 'node-fetch';
import cheerio from 'cheerio';

const url = 'https://www.cptm.sp.gov.br/Pages/Home.aspx';
let tries = 0;

const ids = {
  RUBI: '7',
  DIAMANTE: '8',
  ESMERALDA: '9',
  TURQUESA: '10',
  CORAL: '11',
  SAFIRA: '12'
};

const names = {
  RUBI: 'Rubi',
  DIAMANTE: 'Diamante',
  ESMERALDA: 'Esmeralda',
  TURQUESA: 'Turquesa',
  CORAL: 'Coral',
  SAFIRA: 'Safira'
};

const cliColors = {
  RUBI: 'magentaBright',
  DIAMANTE: 'white',
  ESMERALDA: 'greenBright',
  TURQUESA: 'blueBright',
  CORAL: 'redBright',
  SAFIRA: 'blue'
};


const getCPTM = () =>
  fetch(url).then(data =>
    data.text().then((d) => {
      const $ = cheerio.load(d);
      const json = [];
      const fisrtColumns = {
        Id: '0',
        Color: 'Prata',
        Line: 'Line',
        LineRaw: 'Linha',
        StatusMetro: 'Status',
        chalk: 'white'
      };

      $('.situacao_linhas .col-xs-4').each((index, item) => {
        const name = $(item).children('.nome_linha').text();
        const status = $(item).children().next().text();

        json[index] = {
          Id: ids[name],
          Color: names[name],
          Line: `Linha ${ids[name]} - ${names[name]}`,
          StatusMetro: status,
          chalk: cliColors[name]
        };
      });

      json.unshift(fisrtColumns);
      return json;
    }).catch((error) => {
      return error;
    })
  );


const run = async () => {
  try {
    const a = await getCPTM();
    console.log('success');
    return a;
  } catch (err) {
    console.log('error');
    if (++tries > 5) return err;
    return await run();
  }
}

run().then((aaa) => {
  console.log(aaa);
});



